$primary: #1976d2;
$secondary: #2196f3;
$success: #4caf50;
$danger: #f44336;
$bg-light: #f5faff;
$bg-card: #fff;

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

  .app-title {
    text-align: center;
    color: $primary;
    font-weight: 700;
    font-size: 2.5em;
    margin-bottom: 10px;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.app-bg {
  background: linear-gradient(135deg, $bg-light 0%, #e3f2fd 100%);
  min-height: 100vh;
  font-family: 'Segoe UI', Arial, sans-serif;

  .app-header {
    max-width: 900px;
    margin: 0 auto;
    padding: 40px 20px 20px 20px;

    .app-title {
      text-align: center;
      color: $primary;
      font-weight: 700;
      font-size: 2.5em;
      margin-bottom: 10px;
    }
  }
}

.controls-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 30px 0 20px 0;
  flex-wrap: wrap;
  gap: 20px;
}

.card {
  background: $bg-card;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(33,150,243,0.08);
  padding: 20px;
  min-width: 220px;
  flex: 1 1 220px;
}

.card-title {
  color: $secondary;
  margin-bottom: 10px;
}

.btn {
  font-size: 1em;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  width: 100%;
}

.sign-in-btn {
  background: $secondary;
  color: #fff;
}

.logout-btn {
  background: $danger;
  color: #fff;
  padding: 6px 16px;
  width: auto;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.user-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.user-name {
  font-weight: 500;
}

.select {
  font-size: 1em;
  padding: 8px;
  border-radius: 6px;
  border: 1px solid $secondary;
  width: 100%;
}

.score-row {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin-bottom: 20px;
}

.score-box {
  font-size: 1.2em;
  background: #e3f2fd;
  padding: 10px 24px;
  border-radius: 8px;
  color: $primary;
  font-weight: 600;
}

